import React, { useRef, useEffect, useState, useCallback } from 'react';
import { BrowserMultiFormatReader, NotFoundException } from '@zxing/library';

function BarcodeScanner({ onDetected, onCancel }) {
  const videoRef = useRef(null);
  const [error, setError] = useState(null);
  const [isScanning, setIsScanning] = useState(false);
  const [isInitializing, setIsInitializing] = useState(true);
  const scanningRef = useRef(false);
  const codeReaderRef = useRef(null);
  const streamRef = useRef(null);
  const videoStreamRef = useRef(null);

  // Funkcja walidacji polskich kodw produktw spoywczych
  const validatePolishFoodBarcode = useCallback((code) => {
    const cleanCode = code.replace(/\D/g, ''); // Tylko cyfry
    
    // EAN-13 (13 cyfr) - najczstszy w Polsce
    if (cleanCode.length === 13) {
      return {
        isValid: true,
        code: cleanCode,
        format: 'EAN-13'
      };
    }
    
    // EAN-8 (8 cyfr) - krtszy format
    if (cleanCode.length === 8) {
      return {
        isValid: true,
        code: cleanCode,
        format: 'EAN-8'
      };
    }
    
    // UPC-A (12 cyfr) - produkty importowane z USA
    if (cleanCode.length === 12) {
      return {
        isValid: true,
        code: cleanCode,
        format: 'UPC-A'
      };
    }
    
    return { 
      isValid: false, 
      reason: `Nieprawidowa dugo kodu (${cleanCode.length} cyfr). Produkty spoywcze uywaj 8, 12 lub 13 cyfr.` 
    };
  }, []);

  // Funkcja do penego zatrzymania kamery
  const stopCamera = useCallback(() => {
    console.log(' Zatrzymuj kamer...');
    
    // Zatrzymaj ZXing
    if (streamRef.current) {
      try {
        streamRef.current.stop();
        streamRef.current = null;
      } catch (err) {
        console.log('Bd zatrzymania ZXing controls:', err);
      }
    }

    // Zatrzymaj wszystkie cieki video
    if (videoStreamRef.current) {
      try {
        videoStreamRef.current.getTracks().forEach(track => {
          track.stop();
          console.log(' Zatrzymano track:', track.kind, track.label);
        });
        videoStreamRef.current = null;
      } catch (err) {
        console.log('Bd zatrzymania video tracks:', err);
      }
    }

    // Wyczy video element
    if (videoRef.current) {
      videoRef.current.srcObject = null;
    }

    // Zresetuj ZXing reader
    if (codeReaderRef.current) {
      try {
        codeReaderRef.current.reset();
      } catch (err) {
        console.log('Bd resetowania ZXing:', err);
      }
    }

    scanningRef.current = false;
    setIsScanning(false);
  }, []);

  // Inicjalizacja ZXing
  useEffect(() => {
    let isMounted = true;

    const initializeScanner = async () => {
      try {
        console.log(' Inicjalizuj skaner...');
        
        // Poczekaj chwil na zwolnienie zasobw
        await new Promise(resolve => setTimeout(resolve, 300));
        
        if (!isMounted) return;

        const codeReader = new BrowserMultiFormatReader();
        codeReaderRef.current = codeReader;
        
        console.log(' Skaner zainicjalizowany');
        setIsInitializing(false);
      } catch (err) {
        console.error(' Bd inicjalizacji:', err);
        if (isMounted) {
          setError('Nie mona zainicjalizowa skanera kodw kreskowych');
          setIsInitializing(false);
        }
      }
    };

    initializeScanner();

    return () => {
      isMounted = false;
      console.log(' Czyszczenie inicjalizacji...');
      stopCamera();
    };
  }, [stopCamera]);

  // Gwna funkcja skanowania
  const startScanning = useCallback(async () => {
    if (!codeReaderRef.current || isInitializing) {
      console.log(' Skaner nie jest gotowy, czekam...');
      return;
    }

    // Zatrzymaj poprzednie skanowanie jeli istnieje
    stopCamera();

    try {
      console.log(' Rozpoczynam nowe skanowanie...');
      
      // Poczekaj na zwolnienie zasobw
      await new Promise(resolve => setTimeout(resolve, 500));

      scanningRef.current = true;
      setIsScanning(true);
      setError(null);

      // Pobierz list dostpnych urzdze wideo
      const videoInputDevices = await codeReaderRef.current.listVideoInputDevices();
      console.log(' Dostpne kamery:', videoInputDevices.length);

      if (videoInputDevices.length === 0) {
        throw new Error('Nie znaleziono adnej kamery');
      }

      // Wybierz tyln kamer jeli dostpna
      let selectedDeviceId = undefined;
      
      // Szukaj tylnej kamery
      const backCamera = videoInputDevices.find(device => 
        device.label.toLowerCase().includes('back') || 
        device.label.toLowerCase().includes('rear') ||
        device.label.toLowerCase().includes('environment')
      );
      
      if (backCamera) {
        selectedDeviceId = backCamera.deviceId;
        console.log(' Uywam tylnej kamery:', backCamera.label);
      } else {
        selectedDeviceId = videoInputDevices[0].deviceId;
        console.log(' Uywam pierwszej kamery:', videoInputDevices[0].label);
      }

      // Rozpocznij cige skanowanie
      const controls = await codeReaderRef.current.decodeFromVideoDevice(
        selectedDeviceId,
        videoRef.current,
        (result, error) => {
          if (!scanningRef.current) return;

          if (result) {
            const scannedCode = result.getText();
            console.log(' Znaleziono kod:', scannedCode);

            // Waliduj kod
            const validation = validatePolishFoodBarcode(scannedCode);
            
            if (validation.isValid) {
              console.log(' Kod zaakceptowany, zatrzymuj skanowanie');
              
              // Zatrzymaj skanowanie przed wywoaniem callback
              stopCamera();
              
              // Wywoaj callback po krtkiej przerwie
              setTimeout(() => {
                onDetected(validation.code);
              }, 100);
            }
            // Jeli kod jest nieprawidowy, kontynuuj skanowanie
          }

          if (error && !(error instanceof NotFoundException)) {
            console.log(' Bd skanowania:', error.message);
          }
        }
      );

      // Zapisz kontrolki i strumie
      streamRef.current = controls;
      
      // Zapisz te strumie video dla lepszego cleanup
      if (videoRef.current && videoRef.current.srcObject) {
        videoStreamRef.current = videoRef.current.srcObject;
      }

      console.log(' Skanowanie uruchomione pomylnie');

    } catch (err) {
      console.error(' Bd podczas skanowania:', err);
      
      let errorMessage = 'Nie mona uruchomi skanera kodw kreskowych.';
      
      if (err.name === 'NotAllowedError') {
        errorMessage = 'Dostp do kamery zosta odrzucony. Kliknij ikon kamery w pasku adresu i zezwl na dostp.';
      } else if (err.name === 'NotFoundError') {
        errorMessage = 'Nie znaleziono kamery w urzdzeniu.';
      } else if (err.name === 'NotSupportedError') {
        errorMessage = 'Kamera nie jest obsugiwana przez przegldark.';
      } else if (err.message) {
        errorMessage = `Bd skanera: ${err.message}`;
      }
      
      setError(errorMessage);
      stopCamera();
    }
  }, [validatePolishFoodBarcode, onDetected, stopCamera, isInitializing]);

  // Uruchom skanowanie po zaadowaniu komponentu
  useEffect(() => {
    if (!isInitializing && codeReaderRef.current && !error) {
      const timeoutId = setTimeout(() => {
        startScanning();
      }, 100);

      return () => {
        clearTimeout(timeoutId);
      };
    }
  }, [startScanning, isInitializing, error]);

  // Cleanup przy odmontowaniu komponentu
  useEffect(() => {
    return () => {
      console.log(' Komponent odmontowany, czyszcz wszystko...');
      stopCamera();
    };
  }, [stopCamera]);

  const handleManualInput = () => {
    const barcode = prompt('Wprowad kod kreskowy produktu (8, 12 lub 13 cyfr):');
    if (barcode && barcode.trim()) {
      const validation = validatePolishFoodBarcode(barcode.trim());
      
      if (validation.isValid) {
        stopCamera();
        onDetected(validation.code);
      } else {
        alert(`Nieprawidowy kod kreskowy: ${validation.reason}\n\nProdukty spoywcze uywaj:\n EAN-13 (13 cyfr) - najczciej\n EAN-8 (8 cyfr)\n UPC-A (12 cyfr) - produkty importowane`);
      }
    }
  };

  const handleCancel = () => {
    stopCamera();
    onCancel();
  };

  if (error) {
    return (
      <div className="scanner-container">
        <div className="scanner-header">
          <h3>Bd skanera</h3>
          <button onClick={handleCancel} className="cancel-button"></button>
        </div>
        <div className="scanner-error">
          <p><strong>{error}</strong></p>
          <div style={{ margin: '1rem 0' }}>
            <p><strong>Rozwizania:</strong></p>
            <ul style={{ textAlign: 'left', marginTop: '0.5rem' }}>
              <li>Kliknij ikon kamery w pasku adresu i zezwl na dostp</li>
              <li>Odwie stron (F5)</li>
              <li>Sprawd czy inne aplikacje nie uywaj kamery</li>
              <li>Uyj przegldarki Chrome lub Firefox</li>
              <li>Upewnij si e strona uywa HTTPS</li>
            </ul>
          </div>
          <button onClick={handleManualInput} className="button-primary">
            Wprowad kod rcznie
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="scanner-container">
      <div className="scanner-header">
        <h3>
          {isInitializing 
            ? 'Inicjalizacja skanera...'
            : isScanning 
              ? 'Skanowanie...' 
              : 'Uruchamianie kamery...'}
        </h3>
        <button onClick={handleCancel} className="cancel-button"></button>
      </div>
      
      <div className="scanner-viewport">
        <video 
          ref={videoRef}
          className="scanner-element"
          playsInline
          muted
          autoPlay
          style={{
            width: '100%',
            height: '100%',
            objectFit: 'cover'
          }}
        />
        
        <div className="scanner-overlay">
          <div className="scanner-frame"></div>
          <p className="scanner-instruction">
            {isInitializing
              ? 'Przygotowywanie skanera...'
              : isScanning 
                ? 'Wyceluj w kod kreskowy produktu' 
                : 'adowanie kamery...'}
          </p>
          {isScanning && (
            <div style={{ textAlign: 'center', marginTop: '15px' }}>
              <p style={{ fontSize: '0.8rem', color: 'white', background: 'rgba(0,0,0,0.7)', padding: '10px 15px', borderRadius: '15px' }}>
                Ustaw kod w centrum ramki<br/>
                Trzymaj telefon stabilnie<br/>
                Sprawd owietlenie
              </p>
            </div>
          )}
        </div>
      </div>
      
      <div className="manual-input">
        <button onClick={handleManualInput} className="button-secondary">
          Wprowad kod rcznie
        </button>
        <p style={{ marginTop: '10px', fontSize: '0.8rem', color: '#666' }}>
          Wskazwka: Ustaw telefon 15-20 cm od kodu kreskowego
        </p>
      </div>
    </div>
  );
}

export default BarcodeScanner;
